.form-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 99999999;
  background: rgba(192, 192, 192, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup-form {
  position: relative;
  padding: 50px;
  background: white;
  border: 1px solid #e3e3e3;
  border-radius: 50px;
}
.create-order-input {
  padding: 10px;
}
.close-form {
  position: absolute;
  top: 10px;
  right: 5%;
  cursor: pointer;
}
