.cart {
  background: #f2f2f2;
  width: 100%;
  min-height: 400px;
  position: relative;
}
.cart-header {
  padding: 1.5rem;
  position: relative;
}
.trash-icon {
  position: absolute;
  top: 40%;
  right: 10%;
  cursor: pointer;
}
.cart-body {
  width: 100%;
  height: 250px;
  overflow: auto;
}
.cart-footer {
  width: 100%;
  height: 10%;
  display: grid;
  grid-template-columns: 3fr 5fr;
  bottom: 0;
  padding: 10px;
}
.cart-item-image-wrapper {
  width: 40px;
  height: 40px;
  margin-right: 5px;
}
.total-price-wrapper {
  display: flex;
  justify-content: flex-end;
}

.cart-item {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 5px 10px 5px 10px;
  background: #f2f2f2;
  border-bottom: 1px solid #e3e3e3;
}
.cart-item:first-child {
  border-top: 1px solid #e3e3e3;
}
.cart-item-title {
  word-wrap: break-word;
  height: auto;
  width: 50%;
  font-size: 17px;
}
.cart-item-price {
  font-weight: 700;
  display: flex;
  justify-content: flex-end;
  width: 20%;
}
.cart-item-count {
  color: #bababa;
  font-size: 0.8rem;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 10%;
  position: relative;
}
.cart-item-inc {
  position: absolute;
  right: -100%;
  background: white;
  border: 1px solid #e3e3e3;
  width: 100%;
  height: 110%;
  text-align: center;
  display: none;
  cursor: pointer;
  color: green;
}
.cart-item-count:hover {
  border: 1px solid #e3e3e3;
  background: white;
}
.cart-item-count:hover .cart-item-inc {
  display: block;
}
.cart-item-dec {
  position: absolute;
  left: -100%;
  background: white;
  border: 1px solid #e3e3e3;
  width: 100%;
  height: 110%;
  text-align: center;
  display: none;
  cursor: pointer;
  color: red;
}
.cart-item-count:hover .cart-item-dec {
  display: block;
}

.cart-item-delete {
  margin-left: 10px;
  text-align: center;
  color: red;
}

.cart-widget {
  position: absolute;
  bottom: 0;
  background: #f2f2f2;
  width: 100%;
  height: 70px;
  padding: 5px;
}
