html {
  box-sizing: border-box;
}
h4 {
  margin: 0;
  padding: 0;
}
.category-list-item {
  background: #f2f2f2;
  border: 0;
  border-bottom: 1px solid #e3e3e3;
  padding: 1rem;
  position: relative;
}
.category-list-item:hover {
  background: #e3e3e3;
}
.category-item-count {
  color: #bababa;
  font-size: 0.8rem;
  margin-left: 5px;
}
.category-image {
  width: 90px;
  height: 100px;
  z-index: -1;
  border-radius: 30px;
}
.category-phone-wrapper {
  position: relative;
  border-radius: 20px;
}
.category-display-phone {
  position: absolute;
  top: 0%;
}
.category-list-item-phone {
  background: none;
  border: 0;
  margin-top: 3rem;
  padding: 1rem;
  color: white;
  font-size: 22px;
  font-family: "Roboto", sans-serif;
}
