.None-products {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.spinner {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product-block {
  background: #f2f2f2;
  overflow-y: auto;
  height: 80vh;
  width: 100%;
}

.search-form {
  width: 95%;
  height: 80px;
  background: #f2f2f2;
  position: absolute;
  z-index: 999;
}
.search {
  width: 95%;
  padding-left: 50px;
  position: absolute;
  z-index: 9999999999999999999;
}
.stick {
  position: sticky;
}
.search-button {
  position: absolute;
  top: 1.2rem;
  left: 1.2rem;
  cursor: pointer;
  z-index: 9999999999999999999;
}
.search-icon {
  height: 2rem;
  width: 2rem;
}

.product-group {
  margin-top: 10 0px;
  height: auto;
  width: 100%;
}
.product-edit {
  color: #bababa;
  font-size: 1.5rem;
  margin-left: 5px;
}
.row {
  margin: 0;
  padding: 0;
}
.product-image {
  width: 100%;
  margin-bottom: 0.6rem;
  height: 12rem;
}
.product {
  position: relative;
}
.h6 {
  margin: 0%;
  padding: 0;
}
.price-block {
  position: relative;
  margin-top: 0.5rem;
  height: 2rem;
}
.price-block h4 {
  font-size: 1.2rem;
}
.price_with_discount {
  position: absolute;
  color: orangered;
  top: -12px;
  right: 70%;
}
.add-product {
  position: absolute;
  right: 0.2rem;
  bottom: 0.2rem;
  cursor: pointer;
}
.product-header {
  margin-bottom: 50px;
}

.product-area {
  position: relative;
  background: white;
  margin: 0 1rem 1rem 0;
  padding: 0;
}
.info-product {
  position: absolute;
  top: 0.8rem;
  right: 0.7rem;
}
.info-block {
  word-wrap: break-word;
  display: none;
  color: #bababa;
  font-size: 12px;
  background: white;
  text-align: center;
  position: absolute;
  z-index: 999999;
  top: -20%;
  left: -50%;
  right: -50%;
  border-radius: 10px;
  box-shadow: 0 0 10px #ececec;
}
.info-product:hover .info-block {
  display: block;
}
p {
  padding: 0;
  margin: 0;
}

@media (max-width: 700px) {
  .product-image {
    height: 9rem;
  }
}
@media only screen and (max-width: 700px) {
  .search-form {
    width: 90%;
    background: #f2f2f2;
    position: absolute;
    z-index: 999;
  }
}
